import React from "react"
import { Link, graphql } from "gatsby"
import Layout from "../../components/layout"

const BlogPostExcerpt = ({excerpt, frontmatter, slug}) => (
  <div class="card card-blog mb-3">
    <div class="card-body">
      <h5 class="card-title"> {frontmatter.title}{" "}  </h5>
      <p class="card-text"> {excerpt}
        <br />
        <Link to={slug}> Lire plus</Link>
        </p>
        <p class="card-text">
          <small class="text-muted">  {frontmatter.date} </small>
      </p>
    </div>
  </div>
)

export default ({ data }) => {
  
  const excerpts = data.allMarkdownRemark.edges.map(({ node }) => (
    <BlogPostExcerpt excerpt={node.excerpt} frontmatter={node.frontmatter} slug={node.fields.slug} key={node.id}/>
  ))

  return (
    <Layout>
      <div className="bg-grey">
        <h1 className="blog-title">Blog</h1>
        <div className="container-fluid my-4 blog">
          <div className="row">
            {/* <h4>{data.allMarkdownRemark.totalCount} Posts</h4> */}
            <div className="col-md-7 m-auto">
                    
              {excerpts}
                    
            </div>
            {/* <div className="col-md-3">
              <h5>Articles récents</h5>
              <ul className="list-unstyled">
                <li className="mb-2"> <a className="text-dark" href="<%- url_for(post.path) %>"></a> </li>
              </ul>
              <h5 className="mt-5">Mis en avant</h5>
              <ul className="list-unstyled">
                <li className="mb-2"> <a className="text-dark" href="<%- url_for(post.path) %>"> </a> </li>
              </ul>
            </div> */}
          </div>
        </div>
      </div>
    </Layout>
  )
}

export const query = graphql`
  query {
    allMarkdownRemark(sort: { fields: [frontmatter___date], order: DESC }) {
      totalCount
      edges {
        node {
          id
          frontmatter {
            title
            date(formatString: "DD MMMM YYYY", locale: "fr")
          }
          fields {
            slug
          }
          excerpt
        }
      }
    }
  }
`